import React, {useEffect, useState} from 'react';
import Navbar from '../Components/Navbar';
import { ThemeProvider } from '@material-ui/core/styles';
import ProductCard from '../Components/ProductCard';
import theme from '../theme';
import { Box } from '@material-ui/core';
import {AuthService, ProductService} from '../Services';

function ProductPage(props) {
  const { productId } = props.match.params;

  const [product, setProduct] = useState({});
  const [user, setUser] = useState({});


  useEffect(() => {
    ProductService.getProduct(productId)
      .then(product => {
        setProduct(product)
      })
      .catch(console.log);
    AuthService.getLoggedInUserObject().then(setUser);
  }, []);

  if (!product.categories) {
    return <div/>
  }
  else {
    return (
      <>
        <Navbar register={true} disablePost={false} />
        <Box mt={7}>
          <ThemeProvider theme={theme}>
            <ProductCard
              user={user}
              product={product}
              productname={product.title}
              category={product.categories && product.categories.length && product.categories[0].title}
              oneline={product.tagline}
              description={product.description}
              website={product.website}
              quantity={255}
              upvotesCount={product.upvotesCount}
              commentsCount={product.commentsCount}
              bucketsCount={product.bucketsCount}
              owner={product.submittedBy.firstName + " " + product.submittedBy.lastName}
              username={user.firstName + " " + user.lastName}
              applink={product.appLink}
              comments={product.comments.map(comment => {
                return {
                  value: comment.text,
                  username: comment.user.firstName + " " + comment.user.lastName
                }
              })}
              arr={[1, 2, 3, 4, 5]}
            />
          </ThemeProvider>
        </Box>
      </>
    );
  }
}
export default ProductPage;
